.calculate__title {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    font-family: "Avenir";
    text-transform: uppercase;
    margin-bottom: 25px;
    @media (min-width: 768px) {
        font-size: 18px;
    }
    @media (min-width: 1200px) {
