                @media (min-width: 768px) {
                    content: '';
                }
            }
            &:hover {
                background: none;
                color: rgba(255, 255, 255, 0.23);
                &::before {
                    border: 1px solid #ffffff;
                    border-radius: 50%;
                }
            }
            @media (min-width: 768px) {
                font-size: 14px;
                line-height: 21px;
            }
        }
        .highlighted {
            background: none;
            color: rgba(255, 255, 255, 0.23);
            &::before {
                border: 1px solid #ffffff;
                border-radius: 50%;
            }
